import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			active:1,
			tabActive:0,
			sh:'',
			dataList:[],
			page:1,
			pageSize:20,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
	   	this.getList(this.page)
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods: {
		distributionOrderdetail(){
			uni.navigateTo({
				url:'/pages/distributionOrderdetail/distributionOrderdetail'
			})
		},
		to(where){
			uni.navigateTo({
				url:`/pages/retail/${where}`
			})
		},
		toggleTab(t){
			this.tabActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getList(p){
			var that=this
			var params={
			  page:p,
			  pagesize: this.pageSize
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='/wangtosale_list'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.allnum
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
	}
}
